*********************/
/*common*/

/* ------ pc visible, mobile none --------*/

/* common pc-mobile hide and show styles*/
#wrapper {
    overflow: hidden;
}

.pcpz {
    display: inline;
}

.sppz,
.sppz-block {
    display: none;
}

.hd {
    visibility: hidden;
}

/* ----- Logo ----- */

.logoarea {
    text-align: center;
    position: relative;
    background-color: #fff;
}

/* ----- Top images ----- */

.presentation {
    position: relative;
    width: 100%;
    height: 400px;
    display: block;
    margin-bottom: 0;
}

.ttl-box {
    background-color: #fff;
    padding: 20px;
}

.spl1 {
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("images/splash1.png");
    width: 100%;
    height: 400px;
    display: block;
    background-position: center center;
    background-size: 100% 400px;
    background-repeat: no-repeat;
}

.spl2 {
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("images/splash2.png");
    width: 100%;
    height: 400px;
    display: none;
    background-position: center center;
    background-size: 100% 400px;
    background-repeat: no-repeat;
}

@media (min-width: 1380px) {

    /*for background >1380px an over.*/
    .spl1,
    .spl2 {
        background-size: 100% 400px;
    }
}

@media (max-width: 1380px) {

    /* this is the syle for window less than 1380px */
    .spl1,
    .spl2 {
        background-size: 1380px 400px;
    }
}

.presentation img {
    margin-bottom: 0;
    overflow: hidden;
}

.czone-list {
    dd {
        line-height: 20px;
    }
}

.splashtxt {
    position: absolute;
    left: 0;
    width: 100%;
    top: 116px;
    text-align: center;
    z-index: 1000;
}

/* ----- inside pages image ----- */

.presentation-inside {
    position: relative;
    width: 100%;
    height: 210px;
    display: block;
    margin-bottom: 0;
}

.splash-inside {
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("images/route_bkg.png");
    width: 100%;
    height: 210px;
    display: block;
    background-position: center center;
    background-size: 100% 400px;
    background-repeat: no-repeat;
}

@media (min-width: 1380px) {

    /*for background >1380px an over.*/
    .splash-inside {
        background-size: 100% 210px;
    }
}

@media (max-width: 1380px) {

    /* this is the syle for window less than 1380px */
    .splash-inside {
        background-size: 1380px 210px;
    }
}

.presentation-inside img {
    margin-bottom: 0;
    overflow: hidden;
}

.splashtxt-inside {
    position: absolute;
    left: 0;
    width: 100%;
    top: 76px;
    text-align: center;
    z-index: 1000;
}

/* ----- Pankuzu ----- */

#path {
    clear: both;
}

.topic-path {
    background-color: #fff;
    width: 100%;
    width: 100%;
    border-bottom: 1px solid #dddddd;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    margin-top: -65px;
}

#sidebar {
    li {
        border-bottom: 1px solid #e5e5e5;
    }

    li:last-child {
        border-bottom: none;
    }
}

.pankuzu {
    overflow: hidden;
    padding: 20px;

    li {
        float: left;
        line-height: 1.6em;
        margin-right: 5px;
        white-space: nowrap;
    }
}

ul.pankuzu li {
    a {
        color: #E73F50;

        &:hover,
        &:visited,
        &:active {
            color: #E73F50;
        }
    }

    &:last-child {
        color: #999999;
    }
}

/* ----- main menu ----- */

#flotarea,
#flotarea-index {
    /*required*/
    position: relative;
    overflow: hidden;
    display: block;
    margin-top: -91px;
}

.fixed {
    /*required*/
    position: fixed !important;
    top: 0;
    z-index: 1;

    /*height:253px;*/
    /*calculated by script. The main height control is ".topnavilist ul li" */
    overflow: hidden;
    display: block;
}

#flotarea2 {
    /*required*/
    position: relative;
    overflow: hidden;
    display: block;
}

.fixed-side {
    /*required*/
    position: fixed !important;

    /*top: 153px;*/
    /*calculated by script*/
    z-index: 1;
    width: 29%;
}

.spmenu-list {
    display: none;
    transition: 0.5s;

    &.active_menu__header {
        right: 10px;
        transition: 0.5s;
    }
}

.header-cover {
    width: 100%;
    height: 100%;
    margin-top: -110px;
    position: fixed;
    display: none;
    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.1);
    z-index: 100;
}

#navi {
    background-color: #fff;
    bottom: 0;
    position: relative;
    width: 100%;
}

.topnavi {
    background-color: #ffffff;
    width: 100%;
    overflow: hidden;
    margin-left: auto;
    margin-right: auto;
}

.mmenu1,
.mmenu2,
.mmenu3 {
    border-bottom: 1px solid #dddddd;
}

.mmenu4 {
    border-bottom: 1px solid #dddddd;
    position: relative;
}

.icopopreq {
    margin-left: 6px;

    /*	position:absolute;
	top:33px;
	right:20%;*/
}

.icopopreq-on {
    display: none;
    margin-left: 6px;
}

.icopoptop {
    width: 14px !important;
    height: 12px !important;
    border: none !important;
}

.mmenu4:hover {
    .icopopreq {
        display: none;

        /*	position:absolute;
	top:33px;
	right:20%;*/
    }

    .icopopreq-on {
        display: inline-block;

        /*	position:absolute;
	top:33px;
	right:20%;*/
    }
}

.icopopreq-sp {
    margin-left: 6px;
    border: none !important;
    width: auto !important;
}

.topnavi .topnavilist {
    background-color: #ffffff;
    margin: 0 auto;
    width: 100%;

    ul {
        border-left: 0 none;
        margin: 0 auto;
        overflow: hidden;
        border-top: 1px solid #dddddd;
    }
}

.topnavilist {
    ul li {
        padding: 0px;
        border-right: 1px solid #dddddd;
        float: left;
        height: 90px;

        /*main control of the menu heigh*/
        width: 25%;
        text-align: center;

        &:last-child {
            border-right: none;
        }

        a {
            color: #333333;
            display: block;
            height: 100%;
            line-height: 18px;
            text-align: center;
            text-decoration: none;
            padding-top: 37px;
            padding-bottom: 35px;
            letter-spacing: 1px !important;

            &:hover {
                background-color: #DF0017;
                color: #FFF;
            }
        }
    }

    span {
        display: block;
        color: #333333;
        font-size: 100%;
    }

    a:hover span {
        color: #FFF;
    }

    ul li a {
        display: block;
        height: auto;
        letter-spacing: normal;
    }
}

